
import {computed, reactive, ref, watch, defineComponent, onBeforeUnmount, onMounted, PropType} from 'vue';

import {GridDataItem} from './types';

export default defineComponent({
  name: 'grid-view',

  props: {
    // (gridgrid-template-columns)
    cellWidths: {type: String, required: true},
    // ()
    cellHeight: {type: Number, required: true},
    // 
    data: {type: Array as PropType<GridDataItem[]>, required: true},
  },

  emits: [/*  */ 'cell-click', /*  */ 'infinite-scroll'],

  setup(props, {emit}) {
    // 
    const visibleData = reactive<GridDataItem[]>([]);

    // 
    const visibleColumnCount = ref(1);

    // 
    const maxScrollHeight = computed(() => Math.ceil(
        props.data.length / visibleColumnCount.value * props.cellHeight));

    // ( => https://v3.cn.vuejs.org/guide/composition-api-template-refs.html)
    // 
    const el = ref(null as unknown as HTMLElement);

    //  (,)
    let visibleRowCount = 1;

    // 
    let offsetIndex = 0;

    // (grid)
    let contentWrapper: HTMLElement;

    // resize()
    let resizeObserver: ResizeObserver;

    // 
    let offsetHeight: number = 1;

    // 
    let isAtBottom: boolean = false;

    // 
    let infiniteScrollTimer: number | null = null;

    /**
     * 
     */
    const updateVisibleData = () => {
      //  =  * 
      const visibleCount = visibleRowCount * visibleColumnCount.value;

      // , 
      if (props.data.length <= visibleCount) {
        visibleData.splice(0, visibleData.length, ...props.data);

        isAtBottom = visibleData.length > 0;

        return;
      }


      // | 0 -  1 -  2 -  3 -  4 -  5|  0
      // | 6 -  7 -  8 -  9 - 10 - 11|  208
      // |12 - 13 - 14 - 15 - 16 - 17|  416
      // |18 - 19 - 20 - 21 - 22 - 23|  624

      // 6, 3, ,  top = 208 , 
      // start = top:208 / cellHeight:208 * visibleColumnCount:6

      // (maxScrollHeight.value)
      const top = el.value.scrollTop;
      // ,  scrollTop + height - scrollHeight = 0
      // :  === , (,MDN)scrollTop,  >=
      isAtBottom = top >= maxScrollHeight.value - offsetHeight;

      // 
      let start, end;

      // 
      if (isAtBottom) {
        end = props.data.length;
        start = end - visibleCount;

      } else {
        start = ((top / props.cellHeight) ^ 0) * visibleColumnCount.value;
        end = start + visibleCount;

        //     
        let dValue = end - props.data.length;
        if (dValue >= 0) {
          end = props.data.length;
          start -= dValue;
        }
      }

      // ([start,end])
      let list = props.data.slice(offsetIndex = start, end);
      visibleData.splice(0, visibleData.length, ...list);

      // : *** css position:sticky + top:0 ;  ***
      // y
      // contentWrapper.style.transform = `translate3d(0, ${top}px, 0)`;
    };

    onMounted(() => {
      contentWrapper = el.value.querySelector('.content-wrapper') as HTMLElement;

      // 
      let oldWidth: number, oldHeight: number;

      /**
       * ,,
       *
       * {@link ResizeObserverEntry} contentRect,borderpadding
       */
      resizeObserver = new ResizeObserver(([{contentRect}]) => {
        // 
        const width = Math.max(1, contentRect.width);
        // 
        const height = Math.max(1, contentRect.height);
        // 
        offsetHeight = el.value.offsetHeight;

        // 
        let update = false;

        // (grid)
        let computedStyle;

        // 
        if (oldHeight !== height) {
          // ( '32px')
          const {gridRowGap} = computedStyle = window.getComputedStyle(contentWrapper);
          // 
          let count = Math.ceil((oldHeight = height) / props.cellHeight);

          if (gridRowGap.includes('px')) {
            // (: number * stringnumber => number)
            let gapHeight = --count * (gridRowGap.substring(0, gridRowGap.length - 2) as unknown as number);
            //  = ( - ) / 
            count = Math.ceil((height - gapHeight) / props.cellHeight);

          }

          // 
          if (visibleRowCount !== count) {
            visibleRowCount = count;
            update = true;
          }
        }

        // 
        if (oldWidth !== width) {
          oldWidth = width;
          let count = 1;
          // '0px 0px 0px 0px 0px 0px'
          const {gridTemplateColumns} = computedStyle || window.getComputedStyle(contentWrapper);
          for (let index = gridTemplateColumns.length - 1; index >= 0; --index) {
            if (gridTemplateColumns.charAt(index) === ' ') {
              ++count;
            }
          }

          // 
          if (visibleColumnCount.value !== count) {
            visibleColumnCount.value = count;
            update = true;
          }
        }

        // updatetrue, 
        update && updateVisibleData();
      });

      resizeObserver.observe(el.value);
    });

    // , 
    onBeforeUnmount(() => {
      if (resizeObserver) {
        resizeObserver.unobserve(el.value);
        resizeObserver.disconnect();
      }

      resizeObserver = contentWrapper = null as any;
      isAtBottom = infiniteScrollTimer = offsetHeight = null as any;
    });

    // 
    watch(props.data, updateVisibleData);

    return {
      el, maxScrollHeight, visibleData, updateVisibleData,

      /**
       * 
       *
       * @param event 
       */
      onClick(event: PointerEvent) {
        let target = event.target as HTMLElement;
        if (!target.classList.contains('item-cell')) {
          const parent = target.closest<HTMLElement>('.item-cell');
          target = parent ? parent : target;
        }

        const value = target.getAttribute('data-index');
        if (value) {
          const index = offsetIndex + ((value as unknown as number) ^ 0);

          index >= 0 && index < props.data.length && emit('cell-click', props.data[index]);
        }
      },

      /**
       * (Emitter) , 
       *
       * 1.
       * 2.  
       *
       * @param event   
       */
      infiniteScrollEmitter(event: WheelEvent | TouchEvent) {
        /*
          TODO: , touchstart  touchend  2clientY ;
                , isAtBottom, 
                ,,isAtBottomfalse .

         */
        // , 
        if (!isAtBottom || (event as WheelEvent).deltaY <= 0) {
          return
        }

        // ,
        if (infiniteScrollTimer !== null) {
          clearTimeout(infiniteScrollTimer);
          infiniteScrollTimer = null;
        }

        infiniteScrollTimer = window.setTimeout(() => {
          infiniteScrollTimer = null;
          emit('infinite-scroll')
        }, 500);
      }

    };
  }

});
